$primary-color: #4d4d4d;
$secondary-color: #ffae42;

@mixin media-md {
    @media screen and (min-width:768px){
        @content;
    }
}

@mixin media-lg {
    @media screen and (min-width:1024px){
        @content;
    }
}

@mixin media-xl {
    @media screen and (min-width:1600px){
        @content;
    }
}

